/*
Theme Name: WSU Human Resources Services
Theme URI: https://github.com/washingtonstateuniversity/hrs.wsu.edu/
Author: Adam Turner, WSU University Communications

Stylesheet: Post and Page screens

This stylesheet is loaded on the post and page admin screens.

@since 1.1.0
*/
/**
 * 1.0 Variables
 *
 * SASS variables for colors and sizes.
 *
 *   - 1.01 Colors
 *   - 1.02 Typography
 *   - 1.03 Breakpoints
 *
 * @since 0.11.0
 */
/**
 * 1.01 Colors
 *
 * @since 0.11.0
 */
/* #475055 */
/* #fdfdfd */
/**
 * 1.02 Typography
 *
 * @since 0.11.0
 */
/*
 * The font size steps.
 *
 * These use em units relative to the base context, which is 18px at desktop-
 * sized screens and 16px at smaller-sized screens. As such, all of the font
 * sizes adjust in proportion to each other. Using a 1.125 ratio for the steps.
 *
 * @since 0.11.0
 */
/* 16px small || 18px large */
/**
 * 1.03 Breakpoints
 *
 * @since 0.11.0
 */
/* 544px @ 16px base */
/* 688px @ 16px base */
/* 990px @ 16px base */
body {
  background-color: #fdfdfd;
  color: #191919;
  font-family: "Source Sans Pro", "Lucida Sans", geneva, verdana, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}

@media (min-width: 791px) {
  body {
    font-size: 18px;
  }
}

.wp-block {
  max-width: calc(40em + 28px);
}

/* Width of "wide" blocks */
.wp-block[data-align="wide"] {
  max-width: 1180px;
}

/* Width of "full-wide" blocks */
.wp-block[data-align="full"] {
  max-width: none;
}

/* Centered parent block should not center-align child blocks. */
.block-editor-block-list__block[data-align="center"] .block-editor-block-list__block:not([data-align="center"]) {
  text-align: initial;
}

/**
 * Typography
 */
h1 {
  font-size: 2.281em;
  padding-top: 0;
}

h2 {
  font-size: 1.802em;
}

h3 {
  font-size: 1.424em;
}

h4 {
  font-size: 1.125em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.889em;
}

h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 1em;
}

p {
  font-size: 1em;
}

abbr[title],
acronym[title] {
  border-bottom: 2px dotted;
  text-decoration: none;
}

ins {
  background-color: #ada400;
  color: #000;
  margin: -.05em 0;
  padding: .05em .25em;
}

.has-small-font-size {
  font-size: 0.79em;
}

.has-regular-font-size,
.has-normal-font-size {
  font-size: 1em;
}

.has-medium-font-size {
  font-size: 1.266em;
}

.has-large-font-size {
  font-size: 1.602em;
  line-height: 1.25;
}

.has-larger-font-size {
  font-size: 2.027em;
  line-height: 1.15;
}

/**
 * Links
 */
a {
  border-bottom: 1px solid #da1e00;
  color: #191919;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s ease;
}

a:hover,
a:focus {
  color: #c60c30;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  font-weight: 700;
}

figcaption {
  color: #475055;
  font-size: 0.889em;
  letter-spacing: .02em;
  line-height: normal;
  margin-top: 0;
  padding: .3em .5em 0;
  text-align: inherit;
}

/**
 * Iconography
 *
 * We don't use Dashicons on the front end, but it's easier to approximate the
 * SVG icons from the front end with the already-loaded Dashicons here on the
 * back end.
 */
figcaption:before {
  color: #c60c30;
  content: "\f323";
  display: inline-block;
  width: 1em;
  height: 1em;
  font-size: 1em;
  line-height: 1.4;
  font-family: dashicons;
  text-decoration: inherit;
  font-weight: 400;
  font-style: normal;
  vertical-align: top;
  text-align: center;
  transition: color .1s ease-in 0;
  transform: rotate(45deg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/**
 * Paragraph Styles
 */
p.has-drop-cap:not(:focus):first-letter {
  float: none;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  margin: inherit;
  text-transform: none;
  font-style: normal;
}

p.has-drop-cap:not(:focus):after {
  padding-top: 0;
}

/**
 * 5.02 Preformatted text
 *
 * @since 1.1.0
 */
.wp-block-code,
.wp-block-code .editor-plain-text {
  background-color: #191919;
  color: #fff;
  font-size: 16px;
}

/**
 * 5.05 Buttons
 *
 * @since 0.9.0
 * @since 1.1.0 Reverse defaults and merge WP block syntax.
 */
.wp-block-button,
.wp-block-file {
  margin-bottom: 1em;
}

.wp-block-button__link,
.wp-block-file__button {
  background: #981e32;
  color: #fff;
  border: 1px solid #981e32;
  border-radius: 3px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: 1em;
  letter-spacing: .85px;
  line-height: 1.5;
  overflow-wrap: break-word;
  padding: .75rem 1.5rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all .3s ease;
  white-space: normal;
}

.wp-block-button__link:hover, .wp-block-button__link:focus,
.wp-block-file__button:hover,
.wp-block-file__button:focus {
  background: #fff;
  border: 1px solid #c60c30;
  color: #c60c30;
}

.wp-block-button__link:active,
.wp-block-file__button:active {
  background: #eff0f1;
  border: 1px solid #981e32;
  color: #c60c30;
}

.is-style-outline .wp-block-button__link {
  background: #fff;
  border: 1px solid #981e32;
  color: #981e32;
}

.is-style-outline .wp-block-button__link:hover, .is-style-outline .wp-block-button__link:focus {
  background: #c60c30;
  border: 1px solid #da1e00;
  box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.1);
  color: #fff;
}

.is-style-outline .wp-block-button__link:active {
  background: #6d1624;
  border: 1px solid #475055;
  box-shadow: 0 1px 5px 2px rgba(0, 0, 0, 0.1);
  outline-width: 0;
}

.is-style-small .wp-block-button__link,
.wp-block-file .wp-block-file__button {
  border-radius: 6px;
  padding: .25em .75em;
  vertical-align: baseline;
}

.wp-block-file * + .wp-block-file__button {
  margin-left: .75em;
}

.is-style-squared .wp-block-button__link,
.is-style-text .wp-block-button__link {
  background: transparent;
  color: #981e32;
  border: 1px solid transparent;
  box-shadow: none;
}

.is-style-squared .wp-block-button__link:hover, .is-style-squared .wp-block-button__link:focus,
.is-style-text .wp-block-button__link:hover,
.is-style-text .wp-block-button__link:focus {
  background: #c60c30;
  color: #fff;
}

.is-style-squared .wp-block-button__link:active,
.is-style-text .wp-block-button__link:active {
  background: #6d1624;
  box-shadow: 0 1px 5px 2px rgba(0, 0, 0, 0.1);
}

/**
 * 5.03 Quotes
 *
 * @since 1.1.0
 */
.wp-block-quote.wp-block-quote {
  background-color: #f2f2f2;
  border-left: 4px solid #c60c30;
  font-family: "Source Sans Pro", "Lucida Sans", geneva, verdana, sans-serif;
  margin-bottom: 1em;
  padding: 1em 2em;
  position: relative;
}

.wp-block-pullquote__citation,
.wp-block-quote__citation {
  color: inherit;
  font-size: inherit;
  font-style: italic;
  text-transform: none;
}

.wp-block-pullquote__citation:before,
.wp-block-quote__citation:before {
  content: "- ";
}

.wp-block-quote:before {
  color: #c60c30;
  content: "\201C";
  font-size: 2.281em;
  position: absolute;
  left: 10px;
  top: .5em;
}

.wp-block-quote.is-style-large p,
.wp-block-quote.is-large p {
  font-style: italic;
}

.wp-block-quote.is-style-large p,
.wp-block-quote.is-large p,
.wp-block-pullquote p {
  font-size: 1.602em;
  line-height: 1.5;
}

.wp-block-pullquote {
  border-top: 0;
  border-bottom: 0;
  padding: 3em 0 2em;
  margin-bottom: 1em;
  text-align: center;
}

.wp-block-pullquote blockquote {
  border-top: 2px solid #c60c30;
  border-bottom: 10px solid #c60c30;
  border-radius: 18px;
  font-family: "Source Sans Pro", "Lucida Sans", geneva, verdana, sans-serif;
  margin-bottom: 1em;
  padding: 1em 2em;
}

.wp-block-pullquote .has-text-color a,
.wp-block-pullquote .is-style-solid-color a {
  color: inherit;
  padding: 0 4px;
}

.wp-block-pullquote .has-text-color a:hover, .wp-block-pullquote .has-text-color a:focus,
.wp-block-pullquote .is-style-solid-color a:hover,
.wp-block-pullquote .is-style-solid-color a:focus {
  color: #fff;
  background-color: #c60c30;
}

.wp-block-pullquote .has-text-color a:active,
.wp-block-pullquote .is-style-solid-color a:active {
  color: #fff;
  background-color: #981e32;
}

.wp-block-pullquote:not(.is-style-solid-color) {
  background: none;
}

.wp-block-pullquote.is-style-solid-color {
  border-radius: 4px;
}

.wp-block-pullquote.is-style-solid-color blockquote {
  border: none;
  margin-left: auto;
  margin-right: auto;
  max-width: calc(27rem + 3vw);
  text-align: left;
}

.wp-block-pullquote.is-style-solid-color blockquote p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 2.027em;
}

.wp-block-pullquote.alignleft,
.wp-block-pullquote.alignright,
.wp-block-pullquote.is-style-solid-color.alignleft,
.wp-block-pullquote.is-style-solid-color.alignright {
  text-align: left;
}

.wp-block-pullquote.alignleft blockquote,
.wp-block-pullquote.alignright blockquote,
.wp-block-pullquote.is-style-solid-color.alignleft blockquote,
.wp-block-pullquote.is-style-solid-color.alignright blockquote {
  max-width: 100%;
}

.wp-block-pullquote.alignleft blockquote p,
.wp-block-pullquote.alignright blockquote p,
.wp-block-pullquote.is-style-solid-color.alignleft blockquote p,
.wp-block-pullquote.is-style-solid-color.alignright blockquote p {
  font-size: 1.266em;
}

/**
 * Seperators
 */
.wp-block-separator:not(.is-style-wide):not(.is-style-dots) {
  margin: 0;
  max-width: 5.5em;
}

/**
 * Notifications
 */
.wp-block-hrs-wsu-edu-notifications {
  align-items: center;
  border: 5px solid #475055;
  border-left-width: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  padding: .75em 1em;
}

.wp-block-hrs-wsu-edu-notifications .hrs-wsu-edu-block-notifications__message {
  margin-right: auto;
}

.wp-block-hrs-wsu-edu-notifications img {
  float: left;
  margin-right: .5em;
}

@media (min-width: 43em) {
  .wp-block-hrs-wsu-edu-notifications {
    flex-wrap: nowrap;
  }
  .hrs-wsu-edu-block-notifications__message {
    padding: 0;
  }
  .wp-block-hrs-wsu-edu-notifications .wp-block-button__link {
    margin-left: 1em;
  }
}

/*
 * Callouts
 */
.wp-block-hrs-wsu-edu-callouts {
  border: 8px solid #475055;
  margin: 1.5rem auto;
  max-width: 54.851em;
  padding: 1.5rem 2rem;
}

.wp-block-hrs-wsu-edu-callouts h1,
.wp-block-hrs-wsu-edu-callouts h2,
.wp-block-hrs-wsu-edu-callouts h3,
.wp-block-hrs-wsu-edu-callouts h4,
.wp-block-hrs-wsu-edu-callouts h5,
.wp-block-hrs-wsu-edu-callouts h6 {
  border-bottom: 4px solid #eff0f1;
  color: #475055;
  font-size: 1.602em;
  margin: 0 0 .5em;
  padding: 0 0 .25em;
}

.wp-block-hrs-wsu-edu-callouts.is-style-positive h1,
.wp-block-hrs-wsu-edu-callouts.is-style-positive h2,
.wp-block-hrs-wsu-edu-callouts.is-style-positive h3,
.wp-block-hrs-wsu-edu-callouts.is-style-positive h4,
.wp-block-hrs-wsu-edu-callouts.is-style-positive h5,
.wp-block-hrs-wsu-edu-callouts.is-style-positive h6 {
  border-bottom-color: rgba(173, 164, 0, 0.4);
}

.wp-block-hrs-wsu-edu-callouts.is-style-caution h1,
.wp-block-hrs-wsu-edu-callouts.is-style-caution h2,
.wp-block-hrs-wsu-edu-callouts.is-style-caution h3,
.wp-block-hrs-wsu-edu-callouts.is-style-caution h4,
.wp-block-hrs-wsu-edu-callouts.is-style-caution h5,
.wp-block-hrs-wsu-edu-callouts.is-style-caution h6 {
  border-bottom-color: rgba(255, 184, 28, 0.4);
}

.wp-block-hrs-wsu-edu-callouts.is-style-warning h1,
.wp-block-hrs-wsu-edu-callouts.is-style-warning h2,
.wp-block-hrs-wsu-edu-callouts.is-style-warning h3,
.wp-block-hrs-wsu-edu-callouts.is-style-warning h4,
.wp-block-hrs-wsu-edu-callouts.is-style-warning h5,
.wp-block-hrs-wsu-edu-callouts.is-style-warning h6 {
  border-bottom-color: rgba(246, 134, 31, 0.4);
}

.is-style-positive {
  border-color: #ada400;
}

.is-style-caution {
  border-color: #ffb81c;
}

.is-style-warning {
  border-color: #f6861f;
}

/**
 * Sidebar layouts
 */
@media (min-width: 600px) {
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns > .editor-inner-blocks > .editor-block-list__layout > [data-type="core/column"]:first-child,
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns > .block-editor-inner-blocks > .block-editor-block-list__layout > [data-type="core/column"]:first-child {
    flex-basis: calc(70% - (16px + 28px));
  }
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns > .editor-inner-blocks > .editor-block-list__layout > [data-type="core/column"],
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns > .block-editor-inner-blocks > .block-editor-block-list__layout > [data-type="core/column"]:last-child {
    flex-basis: calc(30% - (16px + 28px));
  }
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns.is-style-sidebar-left > .editor-inner-blocks > .editor-block-list__layout > [data-type="core/column"]:first-child,
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns.is-style-sidebar-left > .block-editor-inner-blocks > .block-editor-block-list__layout > [data-type="core/column"]:first-child {
    flex-basis: calc(30% - (16px + 28px));
  }
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns.is-style-sidebar-left > .editor-inner-blocks > .editor-block-list__layout > [data-type="core/column"]:last-child,
  .wp-block-hrs-wsu-edu-sidebar.wp-block-columns.is-style-sidebar-left > .block-editor-inner-blocks > .block-editor-block-list__layout > [data-type="core/column"]:last-child {
    flex-basis: calc(70% - (16px + 28px));
  }
}
